import React from 'react';
import {GrApple} from "react-icons/gr";
import {GrFacebook} from "react-icons/gr";
import {SingIn} from "./FirebaseManager";
import Box from "./Box";
import ErrorMessage from "./ErrorMessage";
import Button from "./Button";
import {FacebookAuthProvider, getAuth, OAuthProvider, signInWithPopup} from "firebase/auth";
import ConnectWallets from "./ConnectWallets";
import {signInWithCredential} from "@firebase/auth";
import InputField from "./InputField";




class AppleLoginButton extends React.Component {
    render() {
        return (
            <div className='otherLoginButtons'>
                <table>
                    <tr>
                        <td>
                            <div onClick={this.props.handleAppleClick} className="btnAppleLogin">
                                <GrApple/>
                            </div>
                        </td>
                        <td>
                            <div onClick={this.props.handleFacebookClick} className="btnFacebookLogin">
                                <GrFacebook/>
                            </div>
                        </td>

                    </tr>
                </table>
            </div>


        );
    }
}


export default class Login extends React.Component {

    constructor() {
        super();
        this.state = {
            isFieldsEmpty: true,
            isEmailValid: false,
            emailFieldLabel: "email",
            passFieldLabel: "password",
            user:undefined,
            error: {
                email: false,
                emailField: false,
                pass: false,
                message: ""
            },
            emailValue: "",
            passValue: ""
        };
        this.handleInput = this.handleInput.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleAppleClick = this.handleAppleClick.bind(this);
        this.handleFacebookClick = this.handleFacebookClick.bind(this);
    }


    handleInput(field, value) {
        switch (field) {
            case "email":
                console.log(field);
                this.setState(
                    {
                        emailValue: value
                    },
                    () => this.isEmailValid()
                );
                break;
            case "password":
                console.log(field);
                this.setState(
                    {
                        passValue: value
                    },
                    () => this.isFieldsEmpty()
                );
                break;
            default:
                break;
        }
    }

    isEmailValid() {
        if (this.state.emailValue) {
            console.log(this.state.emailValue);
            let email = this.state.emailValue;
            let lastAtPos = email.lastIndexOf("@");
            let lastDotPos = email.lastIndexOf(".");

            if (
                lastAtPos < lastDotPos &&
                lastAtPos > 0 &&
                email.indexOf("@@") == -1 &&
                lastDotPos > 2 &&
                email.length - lastDotPos > 2
            ) {
                console.log("here");
                this.setState(prevState => ({
                    error: {
                        ...prevState.error,
                        email: false
                    }
                }));
            } else {
                console.log("ohoh");
                this.setState(prevState => ({
                    error: {
                        ...prevState.error,
                        email: true,
                        message: "Invalid Email Format"
                    }
                }));
                /*this.setState({
                  formHasError: true,
                  errorMsg: 'Invalid Email Format'
                });*/
            }
        } else {
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    email: false
                }
            }));
        }
    }

    isFieldsEmpty() {
        if (!this.state.emailValue || !this.state.passValue) {
            /*this.setState({
              formHasError: true,
              errorMsg: 'Please fill all details'
            });*/
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    pass: this.state.passValue === "",
                    email: this.state.emailValue === "",
                    message: "Please fill all details"
                }
            }));
            return true;
        } else {
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    pass: false
                }
            }));
            return false;
        }
    }

    handleAppleClick() {
        const provider = new OAuthProvider('apple.com')
        provider.addScope('email');
        provider.addScope('name');

        const auth = getAuth();
        signInWithPopup(auth, provider)
            .then((result) => {
                // The signed-in user info.
                const user = result.user;

                // Apple credential
                const credential = OAuthProvider.credentialFromResult(result);

                signInWithCredential(auth, credential)
                    .then((userCredential) => {
                        // Signed in
                        const _user = userCredential.user;
                        console.log(_user.uid);
                        this.setState(prevState => ({
                            user: _user
                        }));
                        // ...
                    })
                    .catch((error) => {
                        const errorCode = error.code;
                        const errorMessage = error.message;
                        // ...
                    });
                // ...
            })
            .catch((error) => {
                // Handle Errors here.
                const errorCode = error.code;
                const errorMessage = error.message;
                this.setState(prevState => ({
                    error: {
                        ...prevState.error,
                        email: true,
                        pass: true,
                        message: errorMessage
                    }
                }));

                // ...
            });
    }

    handleFacebookClick() {
        console.log('facebook login ....')
        const provider = new FacebookAuthProvider();
        const auth = getAuth();
        signInWithPopup(auth, provider)
            .then((result) => {
                // The signed-in user info.
                const user = result.user;

                // This gives you a Facebook Access Token. You can use it to access the Facebook API.
                const credential = FacebookAuthProvider.credentialFromResult(result);

                signInWithCredential(auth, credential)
                    .then((userCredential) => {
                        // Signed in
                        const _user = userCredential.user;
                        console.log(_user.uid);
                        this.setState(prevState => ({
                            user: _user
                        }));
                        // ...
                    })
                    .catch((error) => {
                        const errorCode = error.code;
                        const errorMessage = error.message;
                        // ...
                    });
                // ...
            })
            .catch((error) => {
                // Handle Errors here.
                const errorCode = error.code;
                const errorMessage = error.message;
                // The email of the user's account used.
                this.setState(prevState => ({
                    error: {
                        ...prevState.error,
                        email: true,
                        pass: true,
                        message: errorMessage
                    }
                }));

                // ...
            });
    }

    handleClick() {
        console.log("Any email error(s): ", this.state.error.email);
        console.log("Any pass error(s): ", this.state.error.pass);
        if (
            !this.isFieldsEmpty() &&
            !this.state.error.email &&
            !this.state.error.pass
        ) {
            SingIn(this.state.emailValue, this.state.passValue).then((_user) => {
                console.log(_user.uid);
                this.setState(prevState => ({
                    user: _user
                }));
            }).catch(msg => {
                this.setState(prevState => ({
                    error: {
                        ...prevState.error,
                        email: true,
                        pass: true,
                        message: msg
                    }
                }));
            });
        }
    }

    render() {
        const user = getAuth().currentUser;
        console.log('user : ' + this.state.user);
        console.log('user : ' + user);
        if(user != null){
            return (
                <ConnectWallets/>
            );
        } else {
            return (
                <div className="loginContainer">
                    <Box>
                        <ErrorMessage
                            msg={this.state.error.message}
                            show={this.state.error.email || this.state.error.pass}
                        />
                        <label className='loginOptionText1'>Hello Again!</label>
                        <label className='loginOptionText2'>Welcome back, you've been missed!</label>
                        <InputField 
                            onChange={this.handleInput}
                            label={this.state.emailFieldLabel}
                            name={this.state.emailFieldLabel}
                            value={this.state.emailValue}
                        />
                        <InputField 
                            onChange={this.handleInput}
                            label={this.state.passFieldLabel}
                            name={this.state.passFieldLabel}
                            value={this.state.passValue}
                            type={this.state.passFieldLabel}
                        />

                        <Button handleClick={this.handleClick}/>
                        <label className='loginOptionText3'>Or Continue
                            with</label>

                        <AppleLoginButton handleAppleClick={this.handleAppleClick} handleFacebookClick={this.handleFacebookClick}/>
                    </Box>
                </div>
            );
        }

    }
}