import React from "react";
import {SingIn} from "./FirebaseManager";
import {getAuth} from "firebase/auth";
import {getDatabase} from "firebase/database";
import {readDatabase} from "./RealtimeDatabase";
import InputField from "./InputField";
import {GrApple, GrFacebook} from "react-icons/gr";
import {approveCANDY, depositFund, withdrawFund} from "./Web3Manager";
import ErrorMessage from "./ErrorMessage";
import {CircularProgress} from "@mui/material";
import ButtonApprove from "./images/ButtonApprove.png";
import ButtonApproved from "./images/ButtonApproved.png";
import ButtonDeposit from "./images/ButtonDeposit.png";
import ButtonWithdraw from "./images/ButtonWithdraw.png";

class ContractCallButtons extends React.Component {
    render() {
        let approved = localStorage.getItem('CANDY_APPROVED');
        return (
            <div className='otherLoginButtons'>
                <table>
                    <tr>
                    <td>
                            <div onClick={this.props.handleApproveClick} className="btn2"><img src={approved ? ButtonApproved: ButtonApprove} alt="blueBtn" className="userProImageBtn"></img></div>
                        </td>
                        <td>
                            <div onClick={this.props.handleDepositClick} className="btn2"><img src={ButtonDeposit} alt="blueBtn" className="userProImageBtn"></img></div>
                            {/* <div onClick={this.props.handleDepositClick} className="btnDepositAndWithdraw">
                                <p>Deposit</p>
                            </div> */}
                        </td>
                        <td>
                        <div onClick={this.props.handleWithdrawClick} className="btn2"><img src={ButtonWithdraw} alt="greenBtn" className="userProImageBtn"></img></div>
                            {/* <div onClick={this.props.handleWithdrawClick} className="btnDepositAndWithdraw">
                                <p>Withdraw</p>
                            </div> */}
                        </td>

                    </tr>
                </table>
            </div>


        );
    }
}

class ProfileInputField extends React.Component {
    constructor() {
        super();
        this.state = {
            type: "text"
        };
    }

    handleInput(e) {
        //const value = e.target.value.replace(/\D/, '');

        this.props.onChange(e.target.value);
    }

    render() {
        return (
            <div>
                <label className='inputFieldLabel'>{this.props.label}</label>
                <input
                    type={this.props.type ? this.props.type : this.state.type}
                    name={this.props.name}
                    onChange={e => this.handleInput(e)}
                    value={this.props.value}
                    className="input"
                />
            </div>
        );
    }
}

class ProfileBox extends React.Component {
    constructor() {
        super();
        this.state = {
            count: 0
        };
    }

    render() {
        return <div className="profileBox">{this.props.children}</div>;
    }
}

export default class UserProfile extends React.Component {

    constructor() {
        super();
        this.state = {
            isFieldsEmpty: true,
            error: {
                amount: false,
                message: "",
                done: false
            },
            amountValue: "",
            user: getAuth().currentUser,
            candy: 0,
            inProgress: false,
        };
        this.handleInput = this.handleInput.bind(this);
        this.handleDepositClick = this.handleDepositClick.bind(this);
        this.handleWithdrawClick = this.handleWithdrawClick.bind(this);
        this.handleApproveClick = this.handleApproveClick.bind(this);
        readDatabase('items/token').then(data => {
            this.setState({
                candy: data
            })
        }).catch(err => {
            console.log(err.message);
        })
    }

    handleInput(value) {
        console.log(value);
        this.setState(
            {
                amountValue: value
            }
        );
    }

    isFieldsEmpty() {
        if (!this.state.emailValue || !this.state.passValue) {
            /*this.setState({
              formHasError: true,
              errorMsg: 'Please fill all details'
            });*/
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: this.state.amountValue === "",
                    message: "Please enter a valid amount"
                }
            }));
            return true;
        } else {
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false
                }
            }));
            return false;
        }
    }

    handleDepositClick() {
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "",
                done:false
            },
            inProgress: true
        }));
        console.log('deposit starting...');
        depositFund(this.state.amountValue).then((r) => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "CANDY withdrawn successfully!",
                    done:true
                }
            }));
            this.setState({
                candy: r
            });
        }).catch(e=>{
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(()=>{
            this.setState({

                inProgress: false
            });
        });
    }

    handleApproveClick(){
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "",
                done:false
            },
            inProgress: true
        }));
        console.log('withdraw starting...');
        approveCANDY().then(r => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "CANDY approved",
                    done:true
                }
            }));
        }).catch(e=>{
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(()=>{
            this.setState({

                inProgress: false
            });
        });
    }

    handleWithdrawClick() {
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "CANDY withdrawn successfully!",
                done:true
            },
            inProgress: true
        }));
        console.log('withdraw starting...');
        withdrawFund(this.state.amountValue).then((r) => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "",
                    done:true
                }
            }));
            this.setState({
                candy: r
            });
        }).catch(e=>{
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(()=>{
            this.setState({

                inProgress: false
            });
        });
    }

    render() {
        const {user, inProgress, candy} = this.state;

        return (
            <div className="loginContainer">

                <ProfileBox>
                    {inProgress && <CircularProgress style={{ position: 'absolute' , left: '50%', top: '50%'}}/>}
                    <ErrorMessage
                        msg={this.state.error.message}
                        show={this.state.error.amount}
                        done={this.state.error.done}
                    />
                    <h4 className='loginOptionText2'>Candy City User ID : {user.uid}</h4>
                    <h4 className='loginOptionText2'>You have : {candy} CANDY in your profile</h4>
                    <br/>
                    <ProfileInputField
                        onChange={this.handleInput}
                        label='Enter amount to deposit or withdraw'
                        name='amount'
                        value={this.state.amountValue}
                    />
                    <ContractCallButtons handleDepositClick={this.handleDepositClick}
                                         handleWithdrawClick={this.handleWithdrawClick}
                                         handleApproveClick={this.handleApproveClick}/>
                </ProfileBox>
            </div>
        );
    }
}