import { getDatabase, ref, onValue, set} from "firebase/database";
import {getAuth} from "firebase/auth";

export const readDatabase = async (path) =>{
    return new Promise((resolve, reject)=>{
        try{
            const db = getDatabase();
            const starCountRef = ref(db, 'users/' + getAuth().currentUser.uid + '/' + path);
            onValue(starCountRef, (snapshot) => {
                const data = snapshot.val();
                let retVal = 0;
                if(data == null || data == 'null')
                    retVal = 0;
                else
                    retVal = parseFloat(data);
                resolve(retVal);
            });
        } catch (e){
            reject(e);
        }
    })

}

export const writeDatabase = async (path, data) =>{
    return new Promise((resolve, reject)=>{
        try{
            const db = getDatabase();
            set(ref(db, 'users/' + getAuth().currentUser.uid + '/' + path), data);
            resolve();
        } catch (e){
            reject(e);
        }
    })

}